
@import url('https://fonts.googleapis.com/css2?family=Libre+Caslon+Display&display=swap');
:root{
--sidebar-flex:1.5;
--condiv-flex:6; 
--fontsize-p:1.5rem;
--fontsize-h1:1.8rem;
--fontsize-h3:1.9rem;
--fontsize-h4:1.9rem;
--fontsize-nav:1.5rem;
--typing-font:3rem;
}

*{
display: flex;
margin:0;
padding: 0;
}
body
{
  display: flex;
font-family: 'Libre Caslon Display';
}
.App
{
display: flex;
width: 100vw;
min-height: 100vh;
}
/* .condiv class is for the right side content container*/
.condiv
{
  display: flex;
  flex-flow: column;
  padding: 100px ;
  background-color: #c2d69d;
  flex:var(--condiv-flex);
}
.condiv h1{
  font-size: x-large;
  text-align: center;
  margin-top: 30px;
}
.profilepic{
      
    margin-left: 305px;
    margin-bottom: -50px;
    width:400px;
    height:400px;
    border-radius: 50%;
    padding:5%;
  }

nav
{
  flex: var(--sidebar-flex);
  background: rgb(168, 90, 16);
  background: linear-gradient(180deg, rgb(214, 240, 101) 30%, rgb(19, 219, 0) 80%);
}
nav ul
{
    font-size:var(--fontsize-nav);
    font-size: 30;
    text-align: center;
    list-style-type: none;
    text-decoration: none;
    color: rgb(255, 0, 0) !important;
}
nav ul li
{
    color: rgb(255, 4, 4) !important;
    margin:40px;
    text-decoration: none;
    
}



.social
{
  position: absolute;
  margin-top: 20px;
  display: flex;
  bottom: 30px;
}
.social a
{
  padding-right: 30px;
  font-size: 25px;
  color: #242e3f;
}
.social a:hover
{
  color: #6e7c96;
}
.subtopic
{
  font-size: x-large;
  margin:10px 10px 30px 10px !important;
}
p
{
  font-size: var(--fontsize-p);
}
h1,h2,h3,h4,p
{
  font-family: 'Libre Caslon Display';
  font-weight: 700;
  color: #21365e;
  margin: 10px;
}
